<template>
  <div class="register-submit">
    <div class="card">
      <img src="../../../assets/image/login/camptogo.png" />
      <div class="zone user-info">
        <p class="title">用户信息</p>
        <p class="info mobile">Mobile: {{ mobile }}</p>
        <p class="info email">Email: {{ email }}</p>
      </div>
      <div class="zone inputZone1">
        <p class="title"><label for="username">用户名</label></p>
        <input type="text" id="username" placeholder="请设置您的用户名" v-model="username" />
      </div>
      <div class="zone inputZone2">
        <p class="title"><label for="password">密码</label></p>
        <input type="password" id="password" placeholder="请设置您的密码" v-model="password" />
      </div>

      <div class="zone button-zone">
        <button @click="submitProviderRegisterInfo">提交</button>
      </div>
    </div>
  </div>
</template>

<script>
import { encode_ez, decode_ez, checkPasswordComplexity } from '../../../common/utils'
import { $submitProviderRegisterInfo } from '../../../network/providerTools/provider'
export default {
  name: 'e-RegisterSubmit',
  created() {
    const str = decode_ez(this.$route.params.encodedStr)
    const len = str.length // 字符串的长度
    const c1 = str.charAt(0) // index = 0的字符
    const c2 = str.charAt(1) // index = 1的字符
    const c3 = str.charAt(len - 2) // index = len - 2的字符
    const c4 = str.charAt(len - 1) // index = len - 1的字符

    if (c1 !== '&' || c2 !== '&' || c3 !== '&' || c4 !== '&') {
      this.$router.replace('/check/error')
      return
    }

    this.mobile = str.slice(9, 20)
    const emailStartIndex = str.indexOf('e', 9) // index = 9之后的e
    this.email = str.slice(emailStartIndex + 6, len - 2)
  },
  data() {
    return {
      mobile: '',
      email: '',
      username: '',
      password: ''
    }
  },
  methods: {
    async submitProviderRegisterInfo() {
      // 检查用户名
      if (this.username === '') {
        this.$toast.warn('用户名不能为空', 0, 170)
        return
      }
      const userNameRegExp = /^[a-zA-Z0-9]{6,20}$/
      const userNameFlag = userNameRegExp.test(this.username)
      // if (!userNameFlag) {
      //   this.$toast.warn(
      //     "用户名长度6-20位，要求包含大、小写字母和数字中的任一项或任意组合",
      //     0,
      //     550,
      //     4000
      //   );
      //   return;
      // }
      // 检查密码
      if (this.password === '') {
        this.$toast.warn('密码不能为空', 0, 170)
        return
      }
      // const passwordRegExp = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,20}$/;
      // const passwordFlag = passwordRegExp.test(this.password);
      // if (!passwordFlag) {
      //   this.$toast.warn(
      //     "密码长度6-20位，要求同时包含大、小写字母和数字，可含有特殊字符",
      //     0,
      //     550,
      //     4000
      //   );
      //   return;
      // }

      // 再去请求服务端
      try {
        const response = await $submitProviderRegisterInfo({
          mobile: encode_ez(this.mobile),
          email: this.email,
          username: this.username,
          password: encode_ez(this.password)
        })
        console.log(response)
        if (response.Code === 200 && response.msg === 'success') {
          this.$toast.success('注册供应商成功')
          await this.$router.replace('/check/success')
          setTimeout(() => {
            this.$router.replace('/tools/login')
          }, 800)
        } else if (response.Code === 999) {
          this.$toast.warn('请先到小程序实名认证账号')
        } else {
          this.$toast.error('不明出错原因，请联系服务商')
        }
      } catch (err) {
        this.$toast.error('提交失败')
      }
    }
  }
}
</script>

